.animation-wrapper {
    width:100%;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 2;
}

.full {
    width: 100%;
    max-width:100%;
}

.col-container {
    width: 100%;
    max-width: 1000px;
    height: 40em;
    max-height: 40em;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    position: static;
}

.animation-container {
    width: 100%;
    max-width: 1000px;
    height: 40em;
    border: 2px solid black;
    // background-color: rgb(120, 120, 223);
    clip-path: polygon(0 0,100% 0,100% 100%,0 100%);
    position: static;
}

.animation-container > img {
    height:40em;
    max-height: 40em;
    z-index: 1;
    position: absolute;
}

.anime {
    animation-timing-function: linear;
    // animation-delay: .5s;
    animation-iteration-count: infinite;
    animation-direction: normal;
}

.grey {
    filter: grayscale(100%);
}

@keyframes slide-right {
    0% {
        left: -100%;
    }
    100% {
        left: 100%;
    }
}

@keyframes slide-left {
    0% {
        right: -100%;
    }
    100% {
        right: 100%;
    }
}



