.settings {
    text-align: center;
}

.occlude {
    height:100%;
    flex: 1 1 auto;
    background-color: white;
    z-index: 100;
}

.pass {
    height:100%;
    flex: 1 1 auto;
    background-color: transparent;
    z-index: 100;
}
